import { Menu, User, Search, Bell, Settings } from "lucide-react";
import { useNavigate } from "react-router-dom";
import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { useResponsive } from "../hooks/useResponsive";

export default function NavBar({ onMenuClick, user, setUser, title = "KOSA" }) {
  const [showAuthPopover, setShowAuthPopover] = useState(false);
  const [showSearch, setShowSearch] = useState(false);
  const navigate = useNavigate();
  const { isMobile } = useResponsive();

  const handleLogout = () => {
    localStorage.removeItem("user");
    setShowAuthPopover(false);
    setUser && setUser(null);
    alert("로그아웃 되었습니다.");
    navigate("/");
  };

  const handleSearchClick = () => {
    if (isMobile) {
      navigate("/search");
    } else {
      setShowSearch(!showSearch);
    }
  };

  if (isMobile) {
    return (
      <div className="fixed top-0 left-0 w-full bg-white shadow-sm border-b border-gray-200 z-50">
        <div className="flex items-center justify-between px-4 py-3" style={{height: 56}}>
          <div className="flex items-center gap-3">
            <Menu 
              size={24} 
              className="cursor-pointer text-gray-700 hover:text-blue-600 transition-colors" 
              onClick={onMenuClick} 
            />
            <span className="text-lg font-bold text-blue-600">{title}</span>
          </div>
          
          <div className="flex items-center gap-2">
            <Search 
              size={20} 
              className="cursor-pointer text-gray-600 hover:text-blue-600 transition-colors" 
              onClick={handleSearchClick}
            />
            <div className="relative">
              <User
                size={20}
                className="cursor-pointer text-gray-600 hover:text-blue-600 transition-colors"
                onClick={() => setShowAuthPopover((v) => !v)}
              />
              <AnimatePresence>
                {showAuthPopover && (
                  <motion.div
                    initial={{ opacity: 0, y: -10, scale: 0.95 }}
                    animate={{ opacity: 1, y: 0, scale: 1 }}
                    exit={{ opacity: 0, y: -10, scale: 0.95 }}
                    className="absolute right-0 mt-2 w-48 bg-white rounded-lg shadow-lg border border-gray-200 p-3 z-20"
                  >
                    {user ? (
                      <>
                        <div className="mb-3 pb-2 border-b border-gray-100">
                          <div className="text-sm text-gray-500">안녕하세요</div>
                          <div className="font-semibold text-gray-800">{user.userId}님</div>
                        </div>
                        <button
                          className="w-full text-left py-2 px-3 hover:bg-gray-50 rounded-lg text-sm transition-colors"
                          onClick={() => { setShowAuthPopover(false); navigate('/mypage'); }}
                        >
                          마이페이지
                        </button>
                        <button
                          className="w-full text-left py-2 px-3 hover:bg-gray-50 rounded-lg text-sm transition-colors text-red-600"
                          onClick={handleLogout}
                        >
                          로그아웃
                        </button>
                      </>
                    ) : (
                      <>
                        <button
                          className="w-full text-left py-2 px-3 hover:bg-gray-50 rounded-lg text-sm transition-colors"
                          onClick={() => { setShowAuthPopover(false); navigate('/login'); }}
                        >
                          로그인
                        </button>
                        <button
                          className="w-full text-left py-2 px-3 hover:bg-gray-50 rounded-lg text-sm transition-colors"
                          onClick={() => { setShowAuthPopover(false); navigate('/signup'); }}
                        >
                          회원가입
                        </button>
                      </>
                    )}
                  </motion.div>
                )}
              </AnimatePresence>
            </div>
          </div>
        </div>
      </div>
    );
  }

  // 데스크톱 버전
  return (
    <div className="fixed top-0 left-0 w-full bg-white shadow-sm border-b border-gray-200 z-50">
      <div className="max-w-7xl mx-auto px-6">
        <div className="flex items-center justify-between h-16">
          {/* 로고 및 메뉴 */}
          <div className="flex items-center gap-8">
            <Menu 
              size={24} 
              className="cursor-pointer text-gray-700 hover:text-blue-600 transition-colors" 
              onClick={onMenuClick} 
            />
            <span className="text-2xl font-bold text-blue-600 cursor-pointer" onClick={() => navigate("/")}>
              {title}
            </span>
          </div>

          {/* 검색바 */}
          <div className="flex-1 max-w-2xl mx-8">
            <AnimatePresence>
              {showSearch && (
                <motion.div
                  initial={{ opacity: 0, width: 0 }}
                  animate={{ opacity: 1, width: "100%" }}
                  exit={{ opacity: 0, width: 0 }}
                  className="relative"
                >
                  <input
                    type="text"
                    placeholder="상품을 검색하세요..."
                    className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    onKeyDown={(e) => {
                      if (e.key === "Enter") {
                        navigate(`/search?query=${encodeURIComponent(e.target.value)}`);
                        setShowSearch(false);
                      }
                    }}
                  />
                  <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={16} />
                </motion.div>
              )}
            </AnimatePresence>
          </div>

          {/* 우측 액션 버튼들 */}
          <div className="flex items-center gap-4">
            <button
              onClick={handleSearchClick}
              className="p-2 rounded-lg hover:bg-gray-100 transition-colors"
            >
              <Search size={20} className="text-gray-600" />
            </button>
            
            <button className="p-2 rounded-lg hover:bg-gray-100 transition-colors relative">
              <Bell size={20} className="text-gray-600" />
              <span className="absolute -top-1 -right-1 w-3 h-3 bg-red-500 rounded-full"></span>
            </button>

            <div className="relative">
              <button
                onClick={() => setShowAuthPopover((v) => !v)}
                className="flex items-center gap-2 p-2 rounded-lg hover:bg-gray-100 transition-colors"
              >
                <User size={20} className="text-gray-600" />
                {user && (
                  <span className="text-sm font-medium text-gray-700">{user.userId}</span>
                )}
              </button>
              
              <AnimatePresence>
                {showAuthPopover && (
                  <motion.div
                    initial={{ opacity: 0, y: -10, scale: 0.95 }}
                    animate={{ opacity: 1, y: 0, scale: 1 }}
                    exit={{ opacity: 0, y: -10, scale: 0.95 }}
                    className="absolute right-0 mt-2 w-56 bg-white rounded-lg shadow-lg border border-gray-200 p-4 z-20"
                  >
                    {user ? (
                      <>
                        <div className="mb-4 pb-3 border-b border-gray-100">
                          <div className="text-sm text-gray-500">안녕하세요</div>
                          <div className="font-semibold text-gray-800 text-lg">{user.userId}님</div>
                        </div>
                        <div className="space-y-1">
                          <button
                            className="w-full text-left py-2 px-3 hover:bg-gray-50 rounded-lg transition-colors flex items-center gap-2"
                            onClick={() => { setShowAuthPopover(false); navigate('/mypage'); }}
                          >
                            <User size={16} />
                            마이페이지
                          </button>
                          <button
                            className="w-full text-left py-2 px-3 hover:bg-gray-50 rounded-lg transition-colors flex items-center gap-2"
                            onClick={() => { setShowAuthPopover(false); navigate('/settings'); }}
                          >
                            <Settings size={16} />
                            설정
                          </button>
                          <button
                            className="w-full text-left py-2 px-3 hover:bg-gray-50 rounded-lg transition-colors text-red-600 flex items-center gap-2"
                            onClick={handleLogout}
                          >
                            로그아웃
                          </button>
                        </div>
                      </>
                    ) : (
                      <>
                        <div className="mb-4">
                          <h3 className="font-semibold text-gray-800 mb-2">로그인</h3>
                          <p className="text-sm text-gray-500">계정에 로그인하여 더 많은 기능을 이용하세요</p>
                        </div>
                        <div className="space-y-2">
                          <button
                            className="w-full py-2 px-4 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors font-medium"
                            onClick={() => { setShowAuthPopover(false); navigate('/login'); }}
                          >
                            로그인
                          </button>
                          <button
                            className="w-full py-2 px-4 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-colors"
                            onClick={() => { setShowAuthPopover(false); navigate('/signup'); }}
                          >
                            회원가입
                          </button>
                        </div>
                      </>
                    )}
                  </motion.div>
                )}
              </AnimatePresence>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
} 