import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import { ChevronRight, Search, X } from "lucide-react";
import { useNavigate } from "react-router-dom";
import { fetchCategories, getMainCategories, getSubcategories, getIconComponent } from "../utils/categoryUtils";

export default function CategoryMenu() {
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchTerm, setSearchTerm] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    const loadCategories = async () => {
      try {
        const data = await fetchCategories();
        setCategories(data);
      } catch (err) {
        console.error('카테고리 로딩 에러:', err);
        setError("카테고리 불러오기 실패");
      } finally {
        setLoading(false);
      }
    };

    loadCategories();
  }, []);

  // 메인 카테고리만 추출
  const mainCategories = getMainCategories(categories);
  
  // 검색 필터링
  const filteredCategories = mainCategories.filter(cat => 
    cat.name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  // 서브카테고리 추출 함수
  function getSubcategoriesForCategory(parentId) {
    return getSubcategories(categories, parentId);
  }

  const handleCategoryClick = (category) => {
    const subcategories = getSubcategoriesForCategory(category.id);
    if (subcategories.length > 0) {
      setSelectedCategory(category);
    } else {
      // 서브카테고리가 없으면 검색 페이지로 이동
      navigate(`/search?category=${encodeURIComponent(category.name)}`);
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <p className="text-gray-500">카테고리를 불러오는 중...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="p-6 text-center">
        <p className="text-red-500 mb-4">{error}</p>
        <button 
          onClick={() => window.location.reload()} 
          className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
        >
          다시 시도
        </button>
      </div>
    );
  }

  if (selectedCategory) {
    const subcategories = getSubcategoriesForCategory(selectedCategory.id);
    return (
      <div className="h-full flex flex-col">
        {/* 헤더 */}
        <div className="flex items-center justify-between p-4 border-b border-gray-200 bg-gray-50">
          <div className="flex items-center gap-3">
            <button
              onClick={() => setSelectedCategory(null)}
              className="p-2 rounded-full hover:bg-gray-200 transition-colors"
            >
              <ChevronRight size={20} className="text-gray-600 rotate-180" />
            </button>
            <div className="flex items-center gap-2">
              {selectedCategory.iconComponent}
              <span className="font-semibold text-gray-800">{selectedCategory.name}</span>
            </div>
          </div>
          <button
            onClick={() => setSearchTerm("")}
            className="p-1 rounded-full hover:bg-gray-200 transition-colors"
          >
            <X size={16} className="text-gray-500" />
          </button>
        </div>

        {/* 검색바 */}
        <div className="p-4 border-b border-gray-200">
          <div className="relative">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={16} />
            <input
              type="text"
              placeholder="서브카테고리 검색..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            />
          </div>
        </div>

        {/* 서브카테고리 목록 */}
        <div className="flex-1 overflow-y-auto">
          {subcategories.length === 0 ? (
            <div className="p-6 text-center text-gray-500">
              하위 카테고리가 없습니다.
            </div>
          ) : (
            <div className="p-4">
              {subcategories
                .filter(sub => sub.name.toLowerCase().includes(searchTerm.toLowerCase()))
                .map((subcategory) => (
                  <motion.div
                    key={subcategory.id}
                    whileHover={{ scale: 1.02 }}
                    whileTap={{ scale: 0.98 }}
                    onClick={() => navigate(`/search?category=${encodeURIComponent(subcategory.name)}`)}
                    className="flex items-center gap-3 p-3 rounded-lg hover:bg-gray-50 cursor-pointer transition-colors border-b border-gray-100 last:border-b-0"
                  >
                    <div className="flex-shrink-0">
                      {subcategory.iconComponent}
                    </div>
                    <span className="flex-1 font-medium text-gray-700">{subcategory.name}</span>
                    <ChevronRight size={16} className="text-gray-400" />
                  </motion.div>
                ))}
            </div>
          )}
        </div>
      </div>
    );
  }

  return (
    <div className="h-full flex flex-col">
      {/* 헤더 */}
      <div className="flex items-center justify-between p-4 border-b border-gray-200 bg-gray-50">
        <h2 className="text-lg font-bold text-gray-800">카테고리</h2>
        <button
          onClick={() => setSearchTerm("")}
          className="p-1 rounded-full hover:bg-gray-200 transition-colors"
        >
          <X size={16} className="text-gray-500" />
        </button>
      </div>

      {/* 검색바 */}
      <div className="p-4 border-b border-gray-200">
        <div className="relative">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={16} />
          <input
            type="text"
            placeholder="카테고리 검색..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
          />
        </div>
      </div>

      {/* 카테고리 목록 */}
      <div className="flex-1 overflow-y-auto">
        {filteredCategories.length === 0 ? (
          <div className="p-6 text-center text-gray-500">
            {searchTerm ? "검색 결과가 없습니다." : "카테고리가 없습니다."}
          </div>
        ) : (
          <div className="p-4">
            {filteredCategories.map((category) => (
              <motion.div
                key={category.id}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                onClick={() => handleCategoryClick(category)}
                className="flex items-center gap-3 p-3 rounded-lg hover:bg-gray-50 cursor-pointer transition-colors border-b border-gray-100 last:border-b-0"
              >
                <div className="flex-shrink-0">
                  {category.iconComponent}
                </div>
                <span className="flex-1 font-medium text-gray-700">{category.name}</span>
                <ChevronRight size={16} className="text-gray-400" />
              </motion.div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
} 