// src/App.jsx
import { BrowserRouter as Router, Routes, Route, unstable_HistoryRouter as HistoryRouter } from "react-router-dom";
import { createBrowserHistory } from 'history';
import MainPage from "./pages/mainpage";
import MobileMainPage from "./pages/MobileMainPage";
import DesktopMainPage from "./pages/DesktopMainPage";
import ResponsiveWrapper from "./components/ResponsiveWrapper";
import LoginPage from "./pages/LoginPage";  
import SignupPage from "./pages/SignupPage";
import ResultPage from "./pages/ResultPage";
import AdminPage from "./pages/AdminPage";
import SearchListPage from "./pages/SearchListPage";
import AnalysisPage from "./pages/AnalysisPage";

// Create a custom history object with future flags
const history = createBrowserHistory({
  future: {
    v7_relativeSplatPath: true,
    v7_startTransition: true,
  },
});

export default function App() {
  return (
    <Router history={history}>
      <Routes>
        <Route path="/" element={
          <ResponsiveWrapper
            mobileComponent={<MobileMainPage />}
            desktopComponent={<DesktopMainPage />}
          />
        } />
        <Route path="/login" element={<LoginPage />} />
        <Route path="/signup" element={<SignupPage />} />
        <Route path="/result/:id" element={<ResultPage />} />
        <Route path="/admin" element={<AdminPage />} />
        <Route path="/search" element={<SearchListPage />} />
        <Route path="/analysis" element={<AnalysisPage />} />
      </Routes>
    </Router>
  );
}
