import React, { useState, useEffect, useCallback, useRef } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import { motion } from "framer-motion";
import { Search, X, Filter, SortAsc, SortDesc, Zap, ChevronDown } from "lucide-react";
import NavBar from "../components/NavBar";
import BottomBar from "../components/BottomBar";
import ProductList from "../components/ProductList";
import { useResponsive } from "../hooks/useResponsive";

// --- Mock Data ---
const topCategories = [
  "전체", "럭셔리", "신발", "상의", "아우터", "하의", "가방", "지갑", "시계",
  "패션잡화", "컬렉터블", "뷰티", "테크", "캠핑", "가구/리빙"
];
const iconCategories = [
  { name: "긴소매 티셔츠", icon: "👕" },
  { name: "인기 티셔츠", icon: "🔥" },
  { name: "샌들/슬리퍼", icon: "🩴" },
  { name: "급상승 볼캡", icon: "🧢" },
  { name: "레인부츠", icon: "👢" },
  { name: "베이프", icon: "🐵" },
  { name: "인기 숏팬츠", icon: "🩳" },
  { name: "아디다스", icon: "👟" },
  { name: "라부부", icon: "🧸" },
  { name: "아식스", icon: "🏃" },
  { name: "장마 대비", icon: "☔" },
];

const FilterButton = ({ children, onClick, isActive }) => (
  <button 
    onClick={onClick}
    className={`px-4 py-2 text-sm border rounded-lg flex items-center gap-2 transition-colors ${
      isActive 
        ? 'bg-blue-600 text-white border-blue-600' 
        : 'border-gray-300 hover:border-gray-800'
    }`}
  >
    {children}
  </button>
);

// --- Component Start ---
export default function SearchListPage() {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const query = searchParams.get("query") || "";
  const category = searchParams.get("category") || "";
  const { isMobile } = useResponsive();

  const [inputValue, setInputValue] = useState(() => query || "");
  const [loading, setLoading] = useState(false);
  const [products, setProducts] = useState([]);
  const [error, setError] = useState(null);
  const [searchStatus, setSearchStatus] = useState("idle");
  const [searchTimeout, setSearchTimeout] = useState(null);
  const [sortBy, setSortBy] = useState("relevance");
  const [showFilters, setShowFilters] = useState(false);
  const [activeTopCat, setActiveTopCat] = useState("전체");
  
  // AbortController를 위한 ref
  const abortControllerRef = useRef(null);

  // max_link는 3으로 고정
  const MAX_LINK = 10;

  // 폴링 함수: DB에 max_link개 저장될 때까지 대기
  const pollForResults = useCallback(async (keyword, maxLink = MAX_LINK, maxWaitMs = 60000, intervalMs = 2000) => {
    const startTime = Date.now();

    while (Date.now() - startTime < maxWaitMs) {
      // AbortController가 취소되었는지 확인
      if (abortControllerRef.current?.signal.aborted) {
        throw new Error("검색이 취소되었습니다.");
      }
      try {
        // 상품 개수 조회
        const countRes = await fetch(`https://kosa-backend-315281980252.asia-northeast3.run.app/api/products/count?q=${encodeURIComponent(keyword)}`, {
          signal: abortControllerRef.current?.signal
        });
        const countData = await countRes.json();
        if (countData.count >= maxLink) {
          // 상품 목록 조회
          const listRes = await fetch(`https://kosa-backend-315281980252.asia-northeast3.run.app/api/products?q=${encodeURIComponent(keyword)}`);
          const listData = await listRes.json();
          return listData.products;
        }
      } catch (err) {
        if (err.name === 'AbortError') {
          throw new Error("검색이 취소되었습니다.");
        }
        console.error("폴링 중 오류:", err);
        throw new Error("상품을 불러오는 중 오류가 발생했습니다.");
      }
      await new Promise(resolve => setTimeout(resolve, intervalMs));
    }
    throw new Error("검색 시간이 초과되었습니다. 잠시 후 다시 시도해주세요.");
  }, []);

  const handleSearch = useCallback(async () => {
    const searchKeyword = query || category;
    if (!searchKeyword.trim() || searchKeyword.trim().length < 2) return;

    // 이전 검색이 진행 중이면 취소
    if (abortControllerRef.current) {
      abortControllerRef.current.abort();
    }

    // 새로운 AbortController 생성
    abortControllerRef.current = new AbortController();

    setLoading(true);
    setError(null);
    setProducts([]);
    setSearchStatus("searching");

    try {
      const searchResponse = await fetch("https://kosa-backend-315281980252.asia-northeast3.run.app/api/products/search", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ keyword: searchKeyword }),
        signal: abortControllerRef.current.signal
      });

      const data = await searchResponse.json();

      if (!searchResponse.ok) {
        throw new Error(data.error || "검색 중 오류가 발생했습니다.");
      }

      if (data.fromCache) {
        setProducts(data.products);
        setSearchStatus("completed");
      } else {
        setSearchStatus("crawling");
        try {
          const polledProducts = await pollForResults(searchKeyword);
          setProducts(polledProducts);
          setSearchStatus("completed");
        } catch (err) {
          if (err.name !== 'AbortError') {
            console.error("폴링 중 오류:", err);
            setError(err.message || "상품을 가져오는 중 오류가 발생했습니다.");
            setSearchStatus("error");
          }
        }
      }
    } catch (err) {
      if (err.name !== 'AbortError') {
        console.error("검색 오류:", err);
        setError(err.message || "검색 중 오류가 발생했습니다.");
        setSearchStatus("error");
      }
    } finally {
      if (!abortControllerRef.current?.signal.aborted) {
        setLoading(false);
      }
    }
  }, [query, category, pollForResults]);

  useEffect(() => {
    if (searchTimeout) clearTimeout(searchTimeout);

    const searchKeyword = query || category;
    if (searchKeyword && searchKeyword.trim().length >= 2) {
      const newTimer = setTimeout(() => {
        handleSearch();
      }, 300);
      setSearchTimeout(newTimer);
    }
    return () => {
      clearTimeout(searchTimeout);
      if (abortControllerRef.current) abortControllerRef.current.abort();
    };
  }, [query, category, handleSearch]);

  const handleInputChange = (e) => setInputValue(e.target.value);
  const handleInputKeyDown = (e) => {
    if (e.key === "Enter" && inputValue.trim() && inputValue.trim().length >= 2) {
      // 이전 타이머 취소
      if (searchTimeout) {
        clearTimeout(searchTimeout);
      }
      navigate(`/search?query=${encodeURIComponent(inputValue)}`);
    }
  };

  const handleClearSearch = () => {
    setInputValue("");
    navigate("/");
  };

  const sortedProducts = [...products].sort((a, b) => {
    switch (sortBy) {
      case "price_asc":
        return a.price - b.price;
      case "price_desc":
        return b.price - a.price;
      case "name_asc":
        return a.name.localeCompare(b.name);
      case "name_desc":
        return b.name.localeCompare(a.name);
      default:
        return 0;
    }
  });

  const searchKeyword = query || category;

  if (isMobile) {
    return (
      <>
        <NavBar title="검색" />
        <div className="max-w-2xl mx-auto bg-gray-50 min-h-screen pt-16 pb-24">
          {/* 검색바 */}
          <div className="sticky top-16 z-10 bg-white border-b border-gray-200 p-4">
            <div className="flex items-center gap-2">
              <div className="flex-1 relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={16} />
                <input
                  className="w-full pl-10 pr-10 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  type="text"
                  placeholder="상품을 검색하세요"
                  value={inputValue}
                  onChange={handleInputChange}
                  onKeyDown={handleInputKeyDown}
                />
                {inputValue && (
                  <button
                    onClick={handleClearSearch}
                    className="absolute right-3 top-1/2 transform -translate-y-1/2 p-1"
                  >
                    <X size={14} className="text-gray-400" />
                  </button>
                )}
              </div>
              <button
                onClick={() => setShowFilters(!showFilters)}
                className={`p-2 rounded-lg border ${showFilters ? 'bg-blue-50 border-blue-300' : 'bg-white border-gray-300'}`}
              >
                <Filter size={16} className={showFilters ? 'text-blue-600' : 'text-gray-600'} />
              </button>
            </div>

            {/* 필터/정렬 옵션 */}
            {showFilters && (
              <motion.div
                initial={{ opacity: 0, height: 0 }}
                animate={{ opacity: 1, height: "auto" }}
                exit={{ opacity: 0, height: 0 }}
                className="mt-3 pt-3 border-t border-gray-200"
              >
                <div className="flex gap-2 overflow-x-auto pb-2">
                  {[
                    { value: "relevance", label: "관련도순" },
                    { value: "price_asc", label: "가격낮은순", icon: SortAsc },
                    { value: "price_desc", label: "가격높은순", icon: SortDesc },
                    { value: "name_asc", label: "이름순", icon: SortAsc },
                  ].map((option) => (
                    <button
                      key={option.value}
                      onClick={() => setSortBy(option.value)}
                      className={`flex items-center gap-1 px-3 py-1 rounded-full text-sm whitespace-nowrap ${
                        sortBy === option.value
                          ? 'bg-blue-600 text-white'
                          : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                      }`}
                    >
                      {option.icon && <option.icon size={12} />}
                      {option.label}
                    </button>
                  ))}
                </div>
              </motion.div>
            )}
          </div>

          {/* 검색 결과 헤더 */}
          {searchKeyword && (
            <div className="px-4 py-3 bg-white border-b border-gray-200">
              <div className="flex items-center justify-between">
                <h2 className="text-lg font-semibold text-gray-800">
                  "{searchKeyword}" 검색 결과
                </h2>
                <span className="text-sm text-gray-500">
                  {products.length}개 상품
                </span>
              </div>
            </div>
          )}

          {/* 로딩 상태 */}
          {loading && searchStatus !== "crawling" && (
            <div className="flex flex-col items-center py-12">
              <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mb-4"></div>
              <p className="text-gray-600 font-medium">검색 중입니다...</p>
            </div>
          )}

          {/* 크롤링 상태 */}
          {searchStatus === "crawling" && (
            <div className="flex flex-col items-center py-12">
              <div className="flex space-x-2 mb-4">
                <div className="w-3 h-3 bg-blue-600 rounded-full animate-bounce"></div>
                <div className="w-3 h-3 bg-blue-600 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                <div className="w-3 h-3 bg-blue-600 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
              </div>
              <p className="text-gray-600 font-medium text-center">
                상품 정보를 수집 중입니다.<br />
                잠시만 기다려주세요...
              </p>
            </div>
          )}

          {/* 에러 상태 */}
          {error && (
            <div className="p-6 text-center">
              <p className="text-red-500 mb-4">{error}</p>
              <button
                onClick={handleSearch}
                className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
              >
                다시 시도
              </button>
            </div>
          )}

          {/* 검색 결과 */}
          {products.length > 0 ? (
            <div className="p-4">
              <ProductList products={sortedProducts} />
            </div>
          ) : searchStatus === "completed" && products.length === 0 && (
            <div className="flex flex-col items-center py-12">
              <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mb-4">
                <Search size={24} className="text-gray-400" />
              </div>
              <p className="text-gray-500 text-center">
                "{searchKeyword}"에 대한<br />
                검색 결과가 없습니다.
              </p>
            </div>
          )}
        </div>
        <BottomBar />
      </>
    );
  }

  // 데스크톱 버전
  return (
    <>
      <NavBar title="KOSA" />
      <div className="max-w-screen-xl mx-auto px-4 sm:px-6 lg:px-8 pt-24 pb-12">
        <div className="mb-6">
          <h1 className="text-3xl font-bold mb-2">
            {searchKeyword ? `"${searchKeyword}"` : "전체 상품"}
          </h1>
          <p className="text-sm text-gray-600">{products.length}개의 상품</p>
        </div>
        
        {/* 필터 */}
        <div className="flex justify-between items-center mb-4 p-4 border-y border-gray-200">
          <div className="flex items-center gap-2">
            <FilterButton onClick={() => alert('필터 기능 구현 예정')} isActive={false}>
              <Filter size={16} />
              모든 필터
            </FilterButton>
             <FilterButton onClick={() => alert('빠른배송 필터')} isActive={false}>
              <Zap size={16} />
              빠른배송
            </FilterButton>
          </div>
          <div className="flex items-center gap-2">
            <select 
              onChange={(e) => setSortBy(e.target.value)}
              className="px-4 py-2 text-sm border border-gray-300 rounded-lg"
            >
              <option value="relevance">인기순</option>
              <option value="price_asc">가격낮은순</option>
              <option value="price_desc">가격높은순</option>
              <option value="name_asc">이름순</option>
            </select>
          </div>
        </div>

        {/* 로딩/에러/결과 표시 */}
        {loading && (
           <div className="flex justify-center items-center py-16">
             <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
           </div>
        )}
        {error && (
          <div className="text-center py-16">
             <p className="text-red-500 text-lg mb-6">{error}</p>
          </div>
        )}
        {products.length > 0 ? (
          <ProductList products={sortedProducts} />
        ) : searchStatus === "completed" && !loading && (
          <div className="text-center py-16">
            <h3 className="text-xl font-semibold text-gray-800 mb-2">검색 결과가 없습니다</h3>
            <p className="text-gray-500">다른 키워드로 검색해보세요.</p>
          </div>
        )}
      </div>
    </>
  );
}
