import React, { useState, useEffect } from 'react';
import { Star, Heart, Share2, ExternalLink, Bookmark, Zap } from 'lucide-react';
import { useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { useResponsive } from '../hooks/useResponsive';

export default function ProductList({ categoryId, products: propProducts }) {
  const [products, setProducts] = useState(propProducts || []);
  const [loading, setLoading] = useState(!propProducts);
  const [error, setError] = useState(null);
  const [favorites, setFavorites] = useState(new Set());
  const navigate = useNavigate();
  const { isMobile } = useResponsive();

  // 분석 상태 관리
  const [analyzeStatus, setAnalyzeStatus] = useState('idle'); // idle, loading, done, error
  const [analyzeResult, setAnalyzeResult] = useState(null);
  const [selectedProductId, setSelectedProductId] = useState(null);

  useEffect(() => {
    // propProducts가 전달되면 해당 값을 사용
    if (propProducts) {
      setProducts(propProducts);
      setLoading(false);
      return;
    }

    // propProducts가 없을 경우에만 데이터 가져오기
    const fetchProducts = async () => {
      try {
        const url = categoryId 
          ? `/api/products?main_category=${categoryId}`
          : '/api/products';
        const response = await fetch(url);
        const data = await response.json();
        setProducts(data.products || []);
        setLoading(false);
      } catch (err) {
        console.error('상품 조회 에러:', err);
        setError('상품을 불러오는데 실패했습니다.');
        setLoading(false);
      }
    };

    fetchProducts();
  }, [categoryId, propProducts]);

  // 상품 클릭 시 분석 상태 확인 후 AnalysisPage로 이동
  const handleAnalyze = async (productId) => {
    // 바로 분석 페이지로 이동 (분석 상태는 AnalysisPage에서 웹소켓으로 처리)
    navigate(`/analysis?productId=${productId}`);
  };

  const handleFavorite = (e, productId) => {
    e.stopPropagation();
    setFavorites(prev => {
      const newFavorites = new Set(prev);
      if (newFavorites.has(productId)) {
        newFavorites.delete(productId);
      } else {
        newFavorites.add(productId);
      }
      return newFavorites;
    });
  };

  const handleShare = (e, product) => {
    e.stopPropagation();
    if (navigator.share) {
      navigator.share({
        title: product.name,
        text: product.description,
        url: product.url || window.location.href,
      });
    } else {
      // 폴백: URL 복사
      navigator.clipboard.writeText(product.url || window.location.href);
      alert('링크가 복사되었습니다.');
    }
  };

  const formatPrice = (price) => {
    if (!price) return '0원';
    return price.toLocaleString() + '원';
  };

  const formatRating = (rating) => {
    if (!rating) return '0.0';
    return parseFloat(rating).toFixed(1);
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <p className="text-gray-500">상품을 불러오는 중...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="p-6 text-center">
        <p className="text-red-500 mb-4">{error}</p>
        <button 
          onClick={() => window.location.reload()} 
          className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
        >
          다시 시도
        </button>
      </div>
    );
  }

  if (!products || products.length === 0) {
    return (
      <div className="flex flex-col items-center py-12">
        <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mb-4">
          <Star size={24} className="text-gray-400" />
        </div>
        <p className="text-gray-500 text-center">표시할 상품이 없습니다.</p>
      </div>
    );
  }

  if (isMobile) {
    return (
      <div className="space-y-3 p-4">
        {products.map((product, index) => (
          <motion.div
            key={product.id || product.product_code}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: index * 0.1 }}
            className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden cursor-pointer active:scale-95 transition-transform"
            onClick={() => handleAnalyze(product.id || product.product_code)}
          >
            <div className="flex">
              {/* 상품 이미지 */}
              <div className="w-24 h-24 flex-shrink-0">
                <img 
                  src={product.image_url} 
                  alt={product.name}
                  className="w-full h-full object-cover"
                  onError={(e) => {
                    e.target.onerror = null;
                    e.target.src = 'https://via.placeholder.com/96x96?text=No+Image';
                  }}
                />
              </div>
              
              {/* 상품 정보 */}
              <div className="flex-1 p-3">
                <div className="flex items-start justify-between mb-1">
                  <h3 className="font-medium text-sm text-gray-800 line-clamp-2 flex-1">
                    {product.name}
                  </h3>
                  <button
                    onClick={(e) => handleFavorite(e, product.id || product.product_code)}
                    className="flex-shrink-0 ml-2 p-1"
                  >
                    <Heart 
                      size={16} 
                      className={`${favorites.has(product.id || product.product_code) ? 'fill-red-500 text-red-500' : 'text-gray-400'}`} 
                    />
                  </button>
                </div>
                
                <div className="flex items-center mb-2">
                  <Star className="w-3 h-3 text-yellow-400 fill-current" />
                  <span className="ml-1 text-xs text-gray-600">
                    {formatRating(product.rating || product.star_rating)}
                  </span>
                  <span className="ml-2 text-xs text-gray-500">
                    ({product.review_count || 0})
                  </span>
                </div>
                
                <p className="text-blue-600 font-bold text-sm">
                  {formatPrice(product.price || product.final_price)}
                </p>
              </div>
            </div>
          </motion.div>
        ))}
      </div>
    );
  }

  // 데스크톱 버전
  return (
    <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-x-6 gap-y-10">
      {products.map((product, index) => (
        <motion.div
          key={product.id || product.product_code}
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: index * 0.05 }}
          className="flex flex-col gap-3 cursor-pointer group"
          onClick={() => handleAnalyze(product.id || product.product_code)}
        >
          {/* 상품 이미지 */}
          <div className="relative overflow-hidden rounded-lg">
            <img 
              src={product.image_url} 
              alt={product.name}
              className="w-full h-auto aspect-[4/5] object-cover group-hover:scale-105 transition-transform duration-300"
              onError={(e) => {
                e.target.onerror = null;
                e.target.src = 'https://via.placeholder.com/300x375?text=No+Image';
              }}
            />
            {/* 찜하기 버튼 */}
            <button
              onClick={(e) => handleFavorite(e, product.id || product.product_code)}
              className="absolute top-3 right-3 p-2 bg-white/80 backdrop-blur-sm rounded-full shadow-md hover:bg-white transition-colors"
            >
              <Heart 
                size={18} 
                className={`${favorites.has(product.id || product.product_code) ? 'fill-red-500 text-red-500' : 'text-gray-600'}`} 
              />
            </button>
          </div>

          {/* 상품 정보 */}
          <div className="flex flex-col gap-1">
            <p className="font-bold text-base truncate">{product.brand || "Brand"}</p>
            <p className="text-sm text-gray-700 truncate group-hover:underline">{product.name}</p>
            
            {(index % 3 === 0) && (
              <div className="flex items-center gap-1 text-green-600 text-xs font-semibold">
                <Zap size={12} />
                빠른배송
              </div>
            )}
            
            <p className="font-bold text-lg mt-1">
              {formatPrice(product.price || product.final_price)}
            </p>

            {/* 별점 및 리뷰 */}
            <div className="text-xs text-gray-500 mt-1 flex items-center gap-1">
              <Star className="w-3 h-3 text-yellow-400 fill-current" />
              <span>{formatRating(product.rating || product.star_rating)}</span>
              <span>·</span>
              <span>리뷰 {product.review_count || 0}</span>
            </div>
          </div>
        </motion.div>
      ))}
    </div>
  );
} 