import React, { useEffect, useState, useCallback, useRef } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { io } from "socket.io-client";
import NavBar from "../components/NavBar";
import BottomBar from "../components/BottomBar";

const API_BASE_URL = "https://kosa-backend-315281980252.asia-northeast3.run.app";
const WS_URL = "wss://kosa-backend-315281980252.asia-northeast3.run.app";

export default function AnalysisPage() {
  const navigate = useNavigate();
  const location = useLocation();
  const [progress, setProgress] = useState(0);
  const [estimatedTime, setEstimatedTime] = useState(null);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(true);
  const isDummy = new URLSearchParams(location.search).get("dummy") === "true";
  const productIdRef = useRef(
    isDummy
      ? Math.floor(Date.now() / 1000) + Math.floor(Math.random() * 1000)
      : new URLSearchParams(location.search).get("productId")
  );
  const productId = productIdRef.current;

  // 분석 상태 확인 및 요청 함수
  const checkAndStartAnalysis = useCallback(async () => {
    try {
      if (isDummy) {
        // 더미 분석인 경우 바로 분석 요청
        const response = await fetch(`${API_BASE_URL}/api/analyze/dummy`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ productId }),
        });

        if (!response.ok) {
          throw new Error("더미 분석 요청에 실패했습니다.");
        }
        return;
      }

      const res = await fetch(`${API_BASE_URL}/api/analyze/status/${productId}`);
      
      if (res.status === 404) {
        // 분석 정보가 없는 경우 → 분석 요청
        const endpoint = isDummy ? "/api/analyze/dummy" : "/api/analyze";
        
        const response = await fetch(`${API_BASE_URL}${endpoint}`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ productId }),
        });

        if (!response.ok) {
          throw new Error("분석 요청에 실패했습니다.");
        }
        return;
      }

      if (!res.ok) {
        throw new Error("상태 확인에 실패했습니다.");
      }

      const data = await res.json();

      if (data.status === "completed") {
        navigate(`/result/${productId}`);
      } else if (data.status === "failed") {
        setError(data.error || "분석에 실패했습니다.");
      } else {
        // 진행 중 상태
        setProgress(data.progress || 0);
        setEstimatedTime(data.estimatedTime || null);
      }

    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  }, [productId, navigate, location, isDummy]);

  // 소켓 연결
  useEffect(() => {
    if (!productId) {
      navigate("/");
      return;
    }

    const socket = io(WS_URL, { transports: ["websocket"] });

    socket.on(`analysis:${productId}`, (data) => {
      if (data.status === "completed") {
        navigate(`/result/${productId}`);
      } else if (data.status === "failed") {
        setError(data.error || "분석에 실패했습니다.");
      } else if (data.status === "processing") {
        setProgress(data.progress || 0);
        setEstimatedTime(data.estimatedTime || null);
      }
    });

    checkAndStartAnalysis();

    return () => {
      socket.disconnect();
    };
  }, [productId, checkAndStartAnalysis, navigate]);

  // 로딩 상태 처리
  if (loading) {
    return (
      <>
        <NavBar title="KOSA" />
        <div className="max-w-2xl mx-auto p-4 bg-gray-100 min-h-screen pt-16 pb-24 flex flex-col items-center justify-center">
          <div className="text-lg text-gray-700">분석을 준비 중입니다...</div>
        </div>
        <BottomBar />
      </>
    );
  }

  // 에러 화면 처리
  if (error) {
    return (
      <>
        <NavBar title="KOSA" />
        <div className="max-w-2xl mx-auto p-4 bg-gray-100 min-h-screen pt-16 pb-24 flex flex-col items-center justify-center">
          <div className="bg-white rounded-xl shadow-lg p-8 w-full max-w-md text-center">
            <div className="text-red-500 text-4xl mb-4">⚠️</div>
            <h2 className="text-xl font-bold text-gray-800 mb-2">오류가 발생했습니다</h2>
            <p className="text-gray-600 mb-6">{error}</p>
            <button
              onClick={() => navigate(-1)}
              className="bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600 transition"
            >
              이전 페이지로 돌아가기
            </button>
          </div>
        </div>
        <BottomBar />
      </>
    );
  }

  // 정상 분석 진행 화면
  return (
    <>
      <NavBar title="KOSA" />
      <div className="max-w-2xl mx-auto p-4 bg-gray-100 min-h-screen pt-16 pb-24 flex flex-col items-center justify-center">
        <div className="bg-white rounded-xl shadow-lg p-8 w-full max-w-md text-center">
          <div className="animate-spin rounded-full h-16 w-16 border-b-2 border-blue-500 mx-auto mb-4"></div>
          <h2 className="text-xl font-bold text-gray-800 mb-2">리뷰 분석 중입니다</h2>
          <p className="text-gray-600 mb-6">
            잠시만 기다려주세요...
            {estimatedTime && (
              <>
                <br />
                예상 소요 시간: 약 {estimatedTime}초
              </>
            )}
          </p>
          <div className="w-full bg-gray-200 rounded-full h-2.5 mb-4">
            <div
              className="bg-blue-500 h-2.5 rounded-full transition-all duration-300"
              style={{ width: `${progress}%` }}
            ></div>
          </div>
          <p className="text-sm text-gray-500">진행률: {progress}%</p>
        </div>
      </div>
      <BottomBar />
    </>
  );
}
