// 아이콘 이름으로 아이콘 컴포넌트 생성
function getIconComponent(iconName, size = 24) {
  const iconMap = {
    'Monitor': <Monitor size={size} className="text-blue-600" />,
    'Shirt': <Shirt size={size} className="text-green-600" />,
    'UtensilsCrossed': <UtensilsCrossed size={size} className="text-orange-600" />,
    'BookOpen': <BookOpen size={size} className="text-purple-600" />,
    'Dumbbell': <Dumbbell size={size} className="text-red-600" />,
    'Home': <Home size={size} className="text-indigo-600" />,
    'Heart': <Heart size={size} className="text-pink-600" />,
    'Baby': <Baby size={size} className="text-yellow-600" />,
    'Gamepad2': <Gamepad2 size={size} className="text-gray-600" />,
    'Car': <Car size={size} className="text-blue-600" />,
    'Gift': <Gift size={size} className="text-red-600" />,
    'ShoppingCart': <ShoppingCart size={size} className="text-green-600" />,
    'Apple': <Apple size={size} className="text-red-600" />,
    'PawPrint': <PawPrint size={size} className="text-brown-600" />,
    'Camera': <Camera size={size} className="text-black-600" />,
    'Package': <Package size={size} className="text-blue-600" />
  };
  
  return iconMap[iconName] || <Package size={size} className="text-gray-600" />;
}

// DB에서 카테고리 불러오기 (icon 필드 활용)
export async function fetchCategories() {
  try {
    const response = await fetch('https://kosa-backend-315281980252.asia-northeast3.run.app/api/categories');
    if (!response.ok) {
      throw new Error('카테고리 불러오기 실패');
    }
    const data = await response.json();
    
    // DB 데이터에 아이콘 컴포넌트 추가
    return data.map(category => ({
      ...category,
      iconComponent: getIconComponent(category.icon, 24)
    }));
  } catch (error) {
    console.error('카테고리 불러오기 오류:', error);
    // DB 연결 실패 시 기본 데이터 반환
    return defaultCategories.map(category => ({
      ...category,
      iconComponent: getIconComponent(category.icon, 24)
    }));
  }
}

// 메인 카테고리만 추출 (parent_id가 null인 것들)
export function getMainCategories(categories) {
  return categories.filter(cat => cat.parent_id === null);
}

// 특정 카테고리의 하위 카테고리 추출
export function getSubcategories(categories, parentId) {
  return categories.filter(cat => cat.parent_id === parentId);
}

