import React from 'react';
import { motion } from 'framer-motion';
import { ChevronLeft } from 'lucide-react';
import { renderCategoryItem } from '../utils/categoryUtils';

export default function SubcategoryList({ category, subcategories = [], onBack, isDesktop = false }) {
  return (
    <div>
      {/* 헤더 */}
      <div className="flex items-center gap-3 mb-4">
        <motion.button
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
          onClick={onBack}
          className="p-2 rounded-full bg-gray-100 hover:bg-gray-200 transition-colors"
        >
          <ChevronLeft size={20} className="text-gray-600" />
        </motion.button>
        <div className="flex items-center gap-2">
          {category.iconComponent}
          <span className="text-lg font-semibold text-gray-800">
            {category.name}
          </span>
        </div>
      </div>

      {/* 서브카테고리 목록 */}
      {subcategories.length === 0 ? (
        <div className="text-center py-8 text-gray-500">
          하위 카테고리가 없습니다.
        </div>
      ) : (
        <div className={`grid ${isDesktop ? 'grid-cols-4 gap-6' : 'grid-cols-4 gap-3'}`}>
          {subcategories.map((subcategory) => (
            <motion.div
              key={subcategory.id}
              whileHover={{ scale: isDesktop ? 1.02 : 1.05 }}
              whileTap={{ scale: 0.95 }}
              className="cursor-pointer"
            >
              {renderCategoryItem(subcategory, isDesktop)}
            </motion.div>
          ))}
        </div>
      )}
    </div>
  );
} 