import React from 'react';
import { motion } from 'framer-motion';
import { renderCategoryItem } from '../utils/categoryUtils';

export default function CategoryList({ categories = [], onSelect, variant = "grid", isDesktop = false }) {
  if (!categories || categories.length === 0) {
    return (
      <div className="text-center py-8 text-gray-500">
        카테고리가 없습니다.
      </div>
    );
  }

  if (variant === "grid") {
    return (
      <div className={`grid ${isDesktop ? 'grid-cols-4 gap-6' : 'grid-cols-4 gap-3'}`}>
        {categories.map((category) => (
          <motion.div
            key={category.id}
            whileHover={{ scale: isDesktop ? 1.02 : 1.05 }}
            whileTap={{ scale: 0.95 }}
            onClick={() => onSelect(category)}
            className="cursor-pointer"
          >
            {renderCategoryItem(category, isDesktop)}
          </motion.div>
        ))}
      </div>
    );
  }

  // 리스트 형태
  return (
    <div className="space-y-2">
      {categories.map((category) => (
        <motion.div
          key={category.id}
          whileHover={{ scale: 1.02 }}
          whileTap={{ scale: 0.98 }}
          onClick={() => onSelect(category)}
          className="flex items-center space-x-3 bg-white rounded-lg shadow p-3 cursor-pointer transition-all hover:shadow-md"
        >
          <div>
            {category.iconComponent}
          </div>
          <span className="font-medium text-gray-700">
            {category.name}
          </span>
        </motion.div>
      ))}
    </div>
  );
} 